<script>
import {
  getLatLngObj,
  getGroundTracks,
  getSatelliteName,
  getCatalogNumber,
  getCOSPAR,
  getClassification,
  getIntDesignatorYear,
  getIntDesignatorLaunchNumber,
  getIntDesignatorPieceOfLaunch,
  getEpochYear,
  getEpochDay,
  getEpochTimestamp,
  getRevNumberAtEpoch,
  getFirstTimeDerivative,
  getBstarDrag,
  getTleSetNumber,
  isValidTLE,
  getInclination,
  getRightAscension,
  getEccentricity,
  getPerigee,
  getMeanMotion,
  getMeanAnomaly
} from 'tle.js';

var renderPaths = (ctx) => {


  const _tleStr = `ISS (ZARYA)
  1 25544U 98067A   17206.18396726  .00001961  00000-0  36771-4 0  9993
  2 25544  51.6400 208.9163 0006317  69.9862  25.2906 15.54225995 67660`;

  //console.log(ctx.tleStr);

  if (isValidTLE(_tleStr)) {
    getGroundTracks({
      tle: ctx.tleStr,
      startTimeMS: Date.now(), //1502342329860,
      stepMS: 60000,
      isLngLatFormat: false
    }).then(threeOrbitsArr => {
      ctx.previousOrbit.latlngs = threeOrbitsArr[0].map(latLng => {
                                                         return {lat: latLng[0], lng: latLng[1]};
                                                       });


      ctx.nextOrbit.latlngs = threeOrbitsArr[2].map(latLng => {
                                                     return {lat: latLng[0], lng: latLng[1]};
                                                   });

      ctx.currentOrbit.latlngs = threeOrbitsArr[1].map(latLng => {
                                                        return {lat: latLng[0], lng: latLng[1]};
                                                      });

      ctx.currentOrbit.latlngs.push(ctx.nextOrbit.latlngs[0]);

      ctx.satelliteName = getSatelliteName(ctx.tleStr);
      ctx.catalogNumber = getCatalogNumber(ctx.tleStr);
      ctx.cospar = getCOSPAR(ctx.tleStr);
      ctx.classification = getClassification(ctx.tleStr);

      ctx.designatorYear = getIntDesignatorYear(ctx.tleStr);
      ctx.designatorLaunchNumber = getIntDesignatorLaunchNumber(ctx.tleStr);
      ctx.designatorPieceOfLaunch = getIntDesignatorPieceOfLaunch(ctx.tleStr);
      ctx.epochYear = getEpochYear(ctx.tleStr);
      ctx.epochDay = getEpochDay(ctx.tleStr);
      ctx.epochTimestamp = getEpochTimestamp(ctx.tleStr);
      ctx.revNumberAtEpoch = getRevNumberAtEpoch(ctx.tleStr);
      ctx.firstTimeDerivative = getFirstTimeDerivative(ctx.tleStr);
      ctx.bstarDrag = getBstarDrag(ctx.tleStr);

      ctx.tleSetNumber = getTleSetNumber(ctx.tleStr);
      ctx.isValidTLE = isValidTLE(ctx.tleStr);

      ctx.inclination = getInclination(ctx.tleStr);
      ctx.rightAscension = getRightAscension(ctx.tleStr);
      ctx.eccentricity = getEccentricity(ctx.tleStr);
      ctx.perigee = getPerigee(ctx.tleStr);
      ctx.meanMotion = getMeanMotion(ctx.tleStr);
      ctx.meanAnomaly = getMeanAnomaly(ctx.tleStr);

      ctx.currentLatLng = getLatLngObj(ctx.tleStr);

      clearInterval();
      setInterval(() => {
        ctx.currentLatLng = getLatLngObj(ctx.tleStr);
      }, 3000);

    });
  } else {
    // send error message for tle
  }
};

